import Vue from 'vue';
import Router from 'vue-router';
import routes from './routes';

//  Vue-Router 
// * new Vue() 
Vue.use(Router)

const router = new Router({
  //  hash  `#` ; `history`  `History API` ,
  mode: 'history',
  //  `css class `,  <router-link> 
  linkExactActiveClass: 'active',
  // 
  scrollBehavior(to, from, savedPosition) {
    if (to.hash) {
      // 
      return { selector: to.hash }
    } else if (savedPosition) {
      // 
      return savedPosition
    } else {
      // 
      return { x: 0, y: 0 }
    }
  },
  // 
  routes
});

// 
router.beforeEach((to, from, next) => {
  //  router.app  router  Vue 
  //  $options  Vue 
  const app = router.app;
  const store = app.$options.store;
  const auth = store.state.auth;
  //  articleId
  const articleId = to.params.articleId;
  // 
  const user = store.state.user && store.state.user.name;
  // 
  const paramUser = to.params.user;

  // 
  app.$message.hide();
  //  `/auth/` 
  // ,
  if (
    (auth && ~to.path.indexOf('/auth/')) ||
    (!auth && to.meta.auth) ||
    //  articleId 
    (articleId && !store.getters.getArticleById(articleId)) ||
    // ,
    (paramUser && paramUser !== user && !store.getters.getArticleByUid(null, paramUser).length)
  ) {
    next('/')
  } else {
    next()
  }
})

// 
//  next 
router.afterEach((to, from) => {
  const app = router.app;
  const store = app.$options.store;
  //  showMsg
  const showMsg = to.params.showMsg;

  if(showMsg) {
    // showMsg 
    if(typeof showMsg === 'string') {
      app.$message.show(showMsg)
    } else {
      app.$message.show('')
    }
  }
})

export default router;
